@import "./variables.scss";
@import "./mixins.scss";
@import "./global.scss";

.feature {
    background-color: $lightGrayishBlue;
    text-align: center;
  
    @include breakpoint-up(medium){
      text-align: left;
    }
  
    &__introduc {
      margin-bottom: 3.75rem;
  
      @include breakpoint-up(large){
        width: 60%;
      }
    }
  
    &__gridi {
  
      @include breakpoint-up(medium){
        display: flex;
        flex-wrap: wrap;     
      }
    }
  
    &__itemm {
      padding: 0.9375rem;
  
      @include breakpoint-up(medium){
        flex: 0 0 50%;
      }
  
      @include breakpoint-up(large){
        flex: 1;
      }
    }
  
    &__icon {
      margin-bottom: 1.875rem;
  
      @include breakpoint-up(large){
        margin-bottom: 2.75rem;
      }    
    }
  
    &__h2 {
      font-size: 1.25rem;
      font-weight: 300;
      line-height: 1.15;
      color: $darkBlue;
      margin-bottom: 1rem;
  
      @include breakpoint-up(large){
        font-size: 1.5rem;
        margin-bottom: 1.5rem;
      }
    }
  
    &__description {
      font-size: 0.875rem;
      line-height: 1.5;
    }
  }