@import "./variables.scss";
@import "./mixins.scss";
@import "./global.scss";

.hero {
  background-color: $veryLightGray;

  .container {

    @include breakpoint-up(large) {
      display: flex;
      align-items: center;
    }
  }

  &__imago {
    position: relative;
    background-image: url('../../public/assets/bg-intro-mobile.svg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    min-height: 280px;

    @include breakpoint-up(medium) {
      min-height: 400px;
      background-position: center 70%;
    }

    @include breakpoint-up(large) {
      flex: 3;
      order: 2;
      height: 656px;
      background-image: none;
    }

    &::before {

      @include breakpoint-up(large) {
        content: "";
        position: absolute;
        height: 100%;
        background-image: url('../../public/assets/bg-intro-desktop.svg');
        background-repeat: no-repeat;
        background-position: center bottom;
        width: 150%;
        background-size: 122%;
        background-position: 0% 83%;
      }
    }

    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      background-image: url('../../public/assets/image-mockups.png');
      background-size: 93%;
      background-repeat: no-repeat;
      background-position: center bottom;

      @include breakpoint-up(medium) {
        background-size: 52%;
      }

      @include breakpoint-up(large) {
        width: 120%;
        height: 122%;
        background-size: 94%;
        background-position: center 150%;
        left: 22%;
      }
    }
  }

  &__text {
    text-align: center;

    @include breakpoint-up(large) {
      flex: 2;
      order: 1;
      text-align: left;
    }
  }
}